<template>
    <div class="container">
        <h1>{{ $t("title.hashGenerator") }}</h1>
        <p class="lead">
            {{ $t("description.hashGenerator") }}
        </p>
        <div class="form-group">
            <textarea class="form-control" :placeholder="$t('text.text')" v-model="plainText" style="min-height: 250px"
                      @input="autoRun"></textarea>
        </div>
        <div class="form-group row justify-content-md-center">
            <div class="form-group col-12 col-md-4 col-lg-2">
                <select id="inputState" class="form-control" v-model="hashType">
                    <option v-for="item in hashTypes">{{ item }}</option>
                </select>
            </div>
            <div class="form-group col-12 col-md-3 col-lg-2">
                <button type="button" class="btn btn-sm btn-primary btn-block" @click="hashing">{{
                    $t("text.generate")}}
                </button>
            </div>
            <div class="form-group col-12 col-md-2 col-lg-2">
                <div class="form-check">
                    <input class="form-check-input" type="checkbox" value="" id="autoRunCheck" v-model="isAutoRun">
                    <label class="form-check-label" for="autoRunCheck">
                        {{ $t("text.autoRun")}}
                    </label>
                </div>
            </div>
        </div>
        <div class="form-group">
            <TextAreaHasCopy :placeholder="$t('text.hash')" minHeight="250px" :value="hashText"></TextAreaHasCopy>
        </div>
    </div>
</template>

<script>
    import hasha from "hasha";
    import TextAreaHasCopy from "../../components/elements/TextAreaHasCopy.vue";

    export default {
        components: {
            TextAreaHasCopy
        },
        metaInfo() {
            return {
                title: this.$i18n.t('title.hashGenerator'),
                meta: []
            }
        },
        data() {
            return {
                hashTypes: ["md5", "sha1", "sha224", "sha256", "sha384", "sha512", "rmd160"],
                hashType: "sha1",
                isAutoRun: false,
                plainText: "",
                hashText: ""
            }
        },
        methods: {
            hashing() {
                this.hashText = hasha(this.plainText, {algorithm: this.hashType});
            },
            autoRun() {
                if (this.isAutoRun) {
                    this.hashing();
                }
            }
        }
    }
</script>

